.h2Gradient{
  font-size: 4rem;
  letter-spacing: -1px;
  text-align: center;
  font-family: "Starjedi";
  line-height: 1.3;
  font-weight: 900;
  margin: 0;
  background-image: 
  linear-gradient(to right, rgb(255 254 254) 0%, rgb(254 254 255) 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.h3Gradient{
  font-size: 3rem;
    letter-spacing: -4px;
    text-align: center;
    font-family: "Starjedi";
    line-height: 1.3;
    margin: 0;
    background-image: 
    linear-gradient(to right, rgb(255 255 255) 100%, rgb(255 255 255) 100%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: #fffdfd00;
}
.h1Gradient{
  font-size: 2rem;
